<template>
  <div>
    <van-sticky>
      <van-nav-bar title="我的推广" left-arrow @click-left="onClickLeft" />
    </van-sticky>
       <div class="header1">
      <div style="font-size:16px;">{{teamList.userInfo.group_direct}}</div>
      <div style="font-size:16px;">团队总人数</div>
    </div>
       <div class="title">
      <div >
        <p>{{teamList.userInfo.total_direct}}</p>
        <p>直推人数</p>
      </div>
      <div  style="border-left:1px solid #DCDCDC">
        <p>{{teamList.userInfo.totalIntegral}}</p>
         <p>我的业绩</p>
      </div>
    </div>
    <!-- 列表 -->
    <div class="noData" v-if="teamList.length==0">
        暂无数据!
    </div>
    <div class="teamWrap" v-else>
      <div class="teamItem" v-for="item in teamList.temaInfo">
        <van-image
          :src="item.wechat_img"
          class="teamImg leftEl"
          round
        >
          <template v-slot:error>加载失败</template>
        </van-image>
        <div class="leftEl itemCon">
          <div class="teamName">{{item.name}}</div>
          <div class="teamPhone">{{item.member.replace(/(\d{3})\d{4}(\d{4})/, '$1****$2')}}</div>
        </div>
        <div class="rightEl priceWrap">
            <div class="teamName">{{item.nick_name}}</div>
          <div class="teamPhone">团队人数:{{item.group_direct}}</div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { myteam } from "@/api/personal";
export default {
  data() {
    return {
    //   teamsems:[],
      teamList: [],
    };
  },
 created(){
   this.getList();
 },
  methods: {
    getList(){
      myteam().then(res=>{
        this.teamList=res;
      })
    },
    onClickLeft() {
      this.$router.go(-1);
    }
  }
};
</script>
