  .title {
    display: flex;
    width: 100%;
    height: 80px;
    margin-bottom: 10px;
    border-bottom: 1px solid #DCDCDC;
    padding: 15px 0;
    div {
      width: 50%;
      height: 60px;
      display: flex;
      flex-direction: column;
      align-items: center;
      P{
          margin: 0;
          margin-top: 10px;
          font-size: 16px;
      }

    }
  }
  .header1 {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
    height: 55px;
    color: black;
    background: #ffeee7;
    padding-bottom: 10px;
  }
.teamWrap {
  padding: 3px 0;
}
.teamItem {
  padding: 16px 0;
  border-bottom: 1px solid #dcdcdc;
  color: #333;
  font-size: 12px;
  overflow: hidden;
  margin: 0 15px;
}
.teamImg {
  width: 44px;
  height: 44px;
  margin-right: 12px;
}
.teamName {
  font-size: 15px;
  margin-bottom: 13px;
}
